// Loading custom fonts
@font-face {
  font-family: "Akkurat-Light";
  src:url("../../applications/fonts/lineto-akkurat-light.eot");
  src:url("../../applications/fonts/lineto-akkurat-light.eot?#iefix") format("embedded-opentype"),
  url("../../applications/fonts/lineto-akkurat-light.woff2") format("woff2"),
  url("../../applications/fonts/lineto-akkurat-light.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
    font-family: 'Akkurat-Light', Helvetica Neue, Helvetica, Arial, sans-serif;
    font-weight: 200;
    line-height: 1.4;
}

.header-title {
    font-size: 5vmin;
    font-weight: 600;
}

.header-description {
    font-size: 3vmin;
}

form input {
    font-size: 3vmin;
}
.rb-note-default {
    margin: 10px 15px;
}
